import React from 'react';
import {Route, Switch} from 'react-router-dom';
import asyncComponent from '../../../../../util/asyncComponent';

const live = ({match}) => (
  <Switch>
      <Route exact from={`${match.url}/`} component={asyncComponent(() => import('./main.js'))}/>
      <Route exact from={`${match.url}/Info`} component={asyncComponent(() => import('./SiteInfo.js'))}/>
      <Route exact from={`${match.url}/building`} component={asyncComponent(() => import('./BuildingInfo.js'))}/>
      <Route exact from={`${match.url}/floor`} component={asyncComponent(() => import('./FloorInfo.js'))}/>
      
      <Route component={asyncComponent(() => import('app/routes/extraPages/routes/404'))}/>
  </Switch>
);

export default live;